<template>
  <section v-if='!isDexLoaded' class='explore__items'>
    <div class='lds-ellipsis'>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
    </div>
  </section>

  <section class='list' v-else>
    <div v-for='(pkmn, key) in (searched || battledex)' :key='key' :class='`pkmn pokemon-${pkmn.types[0]}`'>
      <img :src='`/2d/${pkmn.slug}.png`' />
    </div>
  </section>
</template>

<script>
import EventBus from '@/event.bus';
import components from "./script/components";
import computed from "./script/computed";
import methods from "./script/methods";
import watchers from "./script/watchers";

export default {
  data() {
    return {
      searched : false
    };
  },
  components,
  computed,
  methods,
  watchers,
  created() {
    this.collectData();
    EventBus.$on("SEARCHED", (payload)=> {
      this.searched = payload
    })

    EventBus.$on("SEARCH-RESET", ()=>{
      this.searched = false
    })

    EventBus.$on("CHANGE-TIER", tier => {
      this.searched = false
      this.$store.commit("dex/SET_BATTLEDEX", false);
      this.collectData(tier)
    })
  },
};
</script>

<style lang="scss" scoped>
@import "./style";
</style>