import axios from 'axios'

const collectData = function (payload) {
    if (!this.isDexLoaded) {
        const tier = (payload || this.$route.params.tier);        
        axios.get(`/pokemon/${tier}`).then(res => {
            this.$store.commit("dex/SET_SELECTED_POKEMON", res.data.pokemon[0]);
            this.$store.commit("dex/SET_BATTLEDEX", res.data.pokemon);
        });
    }
}

export default {
    collectData
}